<template>
  <div class="hello">
    <input class="input-box" placeholder="请输入"/>
    <p>-----------------</p>
    <p>-----------------</p>
    <p>可根据进入离开时activated和deactivated钩子函数来分析缓存过程</p>
    <p>-----------------</p>
    <p>-----------------</p>
    <p class="jump-link">
      <router-link to="/transition">点击这里跳转再点浏览器返回</router-link>
    </p>
  </div>
</template>
 
<script>
export default{
props: [''],
 
components: {
},
 
data(){
    return{
      msg: 'hello world'
    }
},
 
mounted(){},

//只有设置keep-alive时才会触发
activated() {
},

//只有设置keep-alive时才会触发
deactivated() {
},

methods: {},
 
}
</script>
 
<style lang='less' scoped>
 .hello{
   background: #ccc;
   margin-top: 1rem;
   .input-box{
     width: 100px;
     height: 20px;
     border: solid 1px #ccc;
     border-radius: 5px;
     margin-top: 10px;
   }
   .jump-link{
     a{
      color: red;
     }
   }
 }
</style>



